@charset "UTF-8";
#quickFinder {
	padding: vw(20) 0 0; position: relative; overflow: hidden; 
	button:focus { outline: none; }
	.qna_wrap {
		padding: 0 0 vw(20);
		.que_title {
			font-size:vw(56); line-height:vw(64); padding:vw(65) vw(40); text-align:center;
			@include displayFlexCenter;
			@include boxSizing;
		}
		.qna_description { 
			padding:vw(30) vw(50) vw(45); display:none;
			&.video_more {
				.txt_box {
					#videoMoreBtn { 
						display: inline-block; font-size: vw(22); padding: vw(13) vw(30); color: $white; background: linear-gradient(to right, #ea7902, #fac21e); text-shadow: vw(2) vw(2) vw(2) #b14e00;
						@include borderRadius(vw(30));
						@include boxShadow(0 vw(2) vw(15) vw(-10) $black);
						&:after { display: none; }
					} 
				}
			}
			strong { font-size: vw(34); color: #6b6b6b; font-weight: 400; line-height: vw(40); }
			.txt_box {
				padding: vw(30) vw(35); border: vw(2) solid #3b3b3b; margin: vw(15) 0 0; position: relative; background-color: #fafafa; text-align: right;
				@include borderRadius(vw(20));
				@include boxShadow(0 vw(5) vw(20) vw(-10) $black);
				@include boxSizing;
				p { color: #3b3b3b; font-size: vw(36); line-height: vw(42); font-weight: $bold; text-align: left; }
				i { @include boxSizing; }
				.load_more_btn, .close_more_btn {
					display:none; font-size:vw(24); font-weight:$bold; color:#888; margin:vw(15) 0 0; padding:0 vw(30) 0 0; position:relative;
					&:after {
						width:vw(14); height:vw(14); display:block; content:""; position:absolute; right:0; 
						@include borderSet(vw(3), solid, #888);
						border-right-width:0; border-top-width:0;
						@include transform(translate(0, vw(-2)) rotate(-45deg));
					}
					&.active { display: inline-block; }
				}
				.load_more_btn {
					&:after { top: vw(4); }
				}
				.close_more_btn {
					&:after { top: vw(6); }
				}
				.learn_more_btn {
					font-size: vw(22); padding: vw(13) vw(30); color: $white; background: linear-gradient(to right, #ea7902, #fac21e); text-shadow: vw(2) vw(2) vw(2) #b14e00;
					margin:vw(15) 0 0; display: none; 
					@include borderRadius(vw(30));
					@include boxShadow(0 vw(2) vw(15) vw(-10) $black);
					@include transition(box-shadow, .2s);
					&:after { display: none; }
					&.active { display: inline-block; }
					&:hover {
						@include boxShadow(3px 3px 8px #c5c5c5);
					}
				}
			}
		}
		.qna_description02 {
			.txt_box {
				padding:vw(30) vw(35) !important;
				p {font-size:vw(32); line-height:vw(38); font-weight:400;}
				.close_more_btn {
					display: inline-block;
					&:after {
						@include transform(translate(0, vw(3)) rotate(135deg));
					}
				}
			}
		}
	}
	#qnaImgWrap {
		width:100%; height:vw(500);
		@include backgroundSoltC;
	}
	//하단 선택 버튼 영역
	#selectWrap {
		padding:vw(30) vw(45) vw(50);
		@include boxSizing;
		.caution_open_btn { display: none; }
		.select_tit {
			padding: 0 0 0 10px;
			em { color: #b49759; font-size: vw(22); display: block; }
		}
		li {
			button {
				width: 100%; padding: vw(30) vw(70) vw(25) vw(60); background-color: #f4f4f4; font-weight: $bold; color: $gray1;
				@include borderSet(vw(3), solid, #ececec);
				@include borderRadius(4vw);
				@include displayFlexCenter(center, left);
				@include transition;
				@include boxSizing;
				&:hover,
				&:focus { border-color: #686868; }
				&.active { background-color: #464646; border-color: #464646; color: $white; }
				&[disabled],
				&:disabled {
					background-color: #dfdfdf; border-color: #ececec;
					i { @include opacity(.2); }
					p { color: #ccc; }
				}
			}
			p { text-align: left; font-size: vw(36); line-height: vw(44); }
		}
	}
	//스텝별
