				&:nth-of-type(5) button.active i {background-image:url(../../images/step01/btn_icon05_on.png);}
				button { padding: vw(30) vw(70) vw(25) vw(60); }
				i {
					min-width: vw(48); min-height: vw(44); display: block; margin: 0 vw(10) 0 0;
