	.step_move_wrap {
		padding:0 vw(45) vw(60);
		@extend %clearfix;
		.btn_cont { 
			width: 50%; height: vw(80); float: left;
			@extend %clearfix;
		}
		#backStepBtn {
      	height: vw(80); position: relative; float: left; padding: 0 vw(65); color: #cfcfcf; font-size: vw(32);
			background-color: #464646; color: $white; 
			@include borderRadius(vw(80));
			@include transition;
         @include boxSizing;
         &:before {
				width: vw(11); height: vw(19); content: ""; display: inline-block; position: relative; left: vw(-10); top:vw(-1);
            @include background('./../../images/common/back_btn_icon.png', 50%, 50%);
         }
