		}
		#nextStepBtn {
			height:vw(80); position: relative; float:right; padding:0 vw(65); color:$white; font-size:vw(32); 
			background: linear-gradient(to right, #ea7902, #fac21e); text-shadow: vw(2) vw(2) vw(2) #c06e09; right:0; opacity: 0;
			@include borderRadius(vw(80));
         @include boxSizing;
         &:after {
				width: vw(13); height: vw(21); content: ""; display: inline-block; position: relative; left: vw(10); top: vw(1);
            @include background('./../../images/common/next_btn_icon.png', 50%, 50%);
         }
