				width: vw(136); height: vw(117); content: ""; display: block; position: absolute; top: vw(-96); right: vw(4);
				@include background('./../../images/common/shop_now_character.png', 50%, 50%);
			}
		}
	}
	.show_now_wrap {
      padding:0 vw(50) vw(30);
      @include boxSizing;
      #shopNowBtn {
			position: relative; color: #ccc; font-size: vw(30); font-weight: $bold;
			&:after {
				width: vw(10); height: vw(10); content: ""; display: inline-block; border-width: 0 0 vw(3) vw(3); border-style: solid; margin: 0 0 vw(3);
